<template>
  <div class="message warning">
    <div class="inset">
      <div>
        <div class="login-head">
          <h1>注册界面</h1>
          <div class="alert-close"> </div>
        </div>
      </div>
      <div class="aaa">
        <input type="text" v-model="username" placeholder="用户名">
      </div>
      <div class="bbb">
        <input type="password" v-model="password" placeholder="密码"></div>
    </div>
    <div class="ccc">
      <button @click="register" class="large1-button">注册</button></div>
  </div>

</template>

<script>
import axios from 'axios';
export default {
  name:'Register',
  data() {
    return {
      username: '',
      password: ''
    };
  },
  methods: {
    register() {
      axios.post('http://localhost:3000/users', { // 修改这里的请求地址为登录验证的端点
        username: this.username,
        password: this.password,
      })
          .then(res => {

            console.log(res.data);
            location.href = "/";
          })
          .catch(error => {
            // 登录请求失败，显示错误信息给用户
            console.log('登录请求失败', error);
            // 这里可以显示错误信息给用户
          });
    }
  }
}
</script>
